<template>
  <div class="table-striped table-responsive">
    <table class="table mb-0 text-center">
      <thead>
        <tr>
          <th style="width: 5%" scope="col">No</th>
          <th style="width: 10%" scope="col">Kode MK</th>
          <th style="width: 30%" scope="col">Nama MK</th>
          <th style="width: 5%" scope="col">Semester</th>
          <th scope="col">Nama Dosen</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(perkuliahanmhs6, index) in getDataKuliah6"
          :key="perkuliahanmhs6.course_code"
        >
          <td>{{ number(index) }}</td>
          <td>{{ perkuliahanmhs6.course_code }}</td>
          <td class="text-left">{{ perkuliahanmhs6.name_ina }}</td>
          <td class="text-center">{{ perkuliahanmhs6.semester }}</td>
          <td class="text-left">{{ perkuliahanmhs6.lecture }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Semester6Kuliah",
  data() {
    return {
      perkuliahanmhs6: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataKuliah6: "perkuliahan/mahasiswa6",
    }),
  },
  methods: {
    ...mapActions({
      mhskuliah: "perkuliahan/perkuliahan",
    }),
    number: function (index) {
      return index + 1;
    },
    getPerkuliahan() {
      this.mhskuliah();
    },
  },
  mounted() {
    this.getPerkuliahan();
  },
};
</script>
